	module.exports = {"data":{"challengeNode":{"title":"Problem 280: Ant and seeds","description":["A laborious ant walks randomly on a 5x5 grid. The walk starts from the central square. At each step, the ant moves to an adjacent square at random, without leaving the grid; thus there are 2, 3 or 4 possible moves at each step depending on the ant's position.","","At the start of the walk, a seed is placed on each square of the lower row. When the ant isn't carrying a seed and reaches a square of the lower row containing a seed, it will start to carry the seed. The ant will drop the seed on the first empty square of the upper row it eventually reaches.","","What's the expected number of steps until all seeds have been dropped in the top row? ","Give your answer rounded to 6 decimal places."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-280-ant-and-seeds","blockName":"Project Euler","tests":[{"text":"<code>euler280()</code> should return 430.088247.","testString":"assert.strictEqual(euler280(), 430.088247, '<code>euler280()</code> should return 430.088247.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler280() {\n  // Good luck!\n  return true;\n}\n\neuler280();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-281-pizza-toppings","id":"5900f4841000cf542c50ff97"},"slug":"/coding-interview-prep/project-euler/problem-280-ant-and-seeds"}}

